html {
  font-size: 17px;
}

body {
  --menu-text-color: white;
  --menu-inverse-text-color: black;
  --menu-padding: 50px;
  min-height: 100vh;
  background-image: radial-gradient(circle at 0 100%, #ff0044b3, #ff00eeb3), url("../funky-lines.677ef8ad.png");
  margin: 0;
  padding: 0;
  font-family: "Press Start 2P", -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  animation: 120s ease-out infinite pizzaz;
}

h1, h2 {
  color: #4682b4;
}

@keyframes pizzaz {
  0% {
    background-position: 0 0;
  }

  25% {
    background-position: 200% 200%;
  }

  50% {
    background-position: 400% -200%;
  }

  75% {
    background-position: 0 100%;
  }

  100% {
    background-position: 0 0;
  }
}

#volume-toggle {
  z-index: 10000;
  color: var(--menu-text-color);
  opacity: .8;
  padding: .75em;
  position: fixed;
  top: 0;
  right: 0;
}

#volume-toggle > .volume-on, #volume-toggle.on:hover > .volume-on {
  display: none;
}

#volume-toggle.on > .volume-on, #volume-toggle:hover > .volume-on {
  display: inline-block;
}

#volume-toggle > .volume-off {
  margin: 0 .625em 0 0;
}

#volume-toggle > .volume-off, #volume-toggle.on:hover > .volume-off {
  display: inline-block;
}

#volume-toggle.on > .volume-off, #volume-toggle:hover > .volume-off {
  display: none;
}

a {
  color: #0af;
}

button {
  min-width: 50px;
  color: var(--menu-text-color);
  border: 2px solid var(--menu-text-color);
  box-sizing: border-box;
  background-color: #0000;
  outline: none;
  margin: 1em;
  padding: 1em;
  font-family: inherit;
  font-size: .75rem;
  line-height: 1;
}

input[type="text"], input[type="password"] {
  color: var(--menu-text-color);
  border: 2px solid var(--menu-text-color);
  background-color: #0000;
  border-radius: 3px;
  margin: 1em;
  padding: .5em;
  font-size: .75rem;
}

button.selected, button:hover, button:active {
  color: var(--menu-inverse-text-color);
  background-color: var(--menu-text-color);
  outline: none;
}

.score-entry {
  margin: 1em;
}

#game-menu, noscript, #credits-menu, #options-menu, #highscore-menu, #login-menu, #signup-menu, #splash-screen, #highscores-view, #game-over-view, #pause-view, .prompt-screen {
  height: 100vh;
  padding: var(--menu-padding);
  box-sizing: border-box;
  color: var(--menu-text-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#lobby-chat-box {
  max-width: calc(var(--remaining-width) * 2 / 3);
}

#lobby-players {
  max-width: calc(var(--remaining-width) / 3);
}

#lobby-chat-input {
  flex-direction: row;
  flex: none;
  display: flex;
}

#chat-messages-box, #lobby-players-box {
  overflow: auto;
}

#chat-text, #chat-messages-box {
  flex: auto;
}

#game-lobby > #lobby-players {
  grid-area: right;
}

.hidden {
  display: none !important;
}

#game-view {
  color: #000;
  height: 100vh;
  background-color: #333;
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-canvas {
  width: 100vmin;
  height: 100vmin;
  background-color: #6495ed;
}

#game-overlay {
  height: 100vh;
  width: 100vw;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
}

#game-overlay > * {
  pointer-events: all;
}

#game-over-view, #pause-view {
  background-color: #000000b3;
}

.prompt-screen {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  color: #fff;
  background-color: #000000b3;
  padding: 3em;
  position: fixed;
  top: 0;
  left: 0;
}

